.errorToast {
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 10000001;
  position: fixed; 
  display: flex;
  justify-content:center;
  align-items: center; 
}

.errorToast__text {
  max-width: 300px;
  max-height: 300px;
  padding: 15px;
  border-radius: 10px;
  color: #fff;
  font-size: 14px;
  background-color: #000;
}

body {
  margin: 0;
  padding: 0;
  font-family: Hiragino Sans GB, Arial,Helvetica, "\5B8B\4F53", sans-serif;
  background: #f0f0f0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a, div, ins, del {
  text-decoration: none
}
